body, html {
  margin: 0;
  padding: 0; 
}
*{
  box-sizing: border-box;
}
h1,h2 {
  margin: 0;
  padding: 10px;

}
ul{
  list-style: none;
  margin: 0;
  padding: 0;

}

button {
  padding: 5px;
  border: 1px solid #6686b4;
  background: #31589b;
  color: #fff;
}
.headerWrapp{
  background-color: #6f9ed6;
}

.headerWrapp h1 {
  color: #fff; 
}

.headerWrapp ul {
  color: #fff;
  margin: 0;
  padding: 10px;
  list-style: none;
  overflow: hidden;
  background-color: #31589b;
}
.headerWrapp li {
  
  margin: 0;
  padding: 10px;
  float:left;
}
.headerWrapp li a{
  color: #fff;
}

.PokemonWrapp{
  background-color:rgb(196, 194, 209);
  padding: 10px;
}
.pokemonBox{
    width: 200px;
    min-height: 200px;
    text-align: center;
    border: 1px solid #fff;
    padding: 10px;
    margin: 5px;
    font-weight: bold;
    background-color: #dae6f7;
    border-radius: 10px;
}

.flexWrapp{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}
.center{
  text-align: center;
}
input.form-control {
  padding: 5px;
  border-radius: 5px;
  margin-right: 5px
}
.flexInner2{
  padding: 20px;
  border: 1px dotted #ccc;
  background-color: #fff;
}
.pokemonH2{
  background-color: #6ba5dd;
  border-bottom: 2px solid #40539a;
  color: #fff;
}
